import React from 'react'
import { useState } from 'react';
import { Button, Collapse } from 'react-bootstrap';

function Restrvew({rview}) {
    console.log(rview);
    const [open,setOpen]=useState(false);
  return (
    <div>
      <Button variant='dark' onClick={()=>setOpen(!open)}
      aria-controls="example-collapse-text"
      aria-expanded={open} style={{marginLeft:'250px',borderRadius:'20px',marginTop:'10px',marginBottom:'15px'}}>
        Reviews
      </Button>
      <Collapse in={open}>
        <div id="example-collapse-text">
            {
                rview.map(item=>(
                    <>
                    <h6 style={{textAlign:'center',paddingTop:'10px'}}>Name:{item.name}<span>{item.date}</span></h6>
                    <p style={{textAlign:'center',color:'green'}}>Rating:{item.rating}</p>
                    </>
                ))
            }

        </div>
      </Collapse>
    </div>
  )
}

export default Restrvew
