import React,{useState,useEffect} from 'react'
import { useParams } from 'react-router-dom'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Image } from 'react-bootstrap';
import Restoperate from './Restoperate';
import Restrvew from './Restrvew';

function Viewitems() {
  const params=useParams()
  console.log(params.id);

   //to hold values
   const [allRestaurents,setRestaurents]=useState([])
   //function to call API to get all restaurent list from restaurent.json
   const getRestaurent=async ()=>{
     await fetch('/restaurants.json')
     .then((data)=>{
       data.json()
       .then((result)=>{
         setRestaurents(result.restaurants);
       })
     })
   }

   // console.log(allRestaurents);

   useEffect(()=>{
       getRestaurent();
   },[])

   const viewrest=allRestaurents.find(item=>item.id==params.id);
   console.log(viewrest);

  return (
    <>
      {
        viewrest?(
          <Row>
            <Col>
<Image style={{height:'600px',width:'450px',marginLeft:'70px',borderRadius:'15px'}} src={viewrest.photograph} fluid />
            </Col>
            <Col style={{marginTop:'30px'}}>
<h3 style={{textAlign:'center'}}>{viewrest.name}</h3>
<h5 style={{textAlign:'center'}}>{viewrest.neighborhood}</h5>
<h6 style={{textAlign:'center'}}>{viewrest.address}</h6>
<p style={{textAlign:'center'}}>{viewrest.cuisine_type}</p>
<Restoperate operate={viewrest.operating_hours}/>
<Restrvew rview={viewrest.reviews}/>
            </Col>
          </Row>
        ):'null'
      }
    </>
  )
}

export default Viewitems
