import {createStore,combineReducers,applyMiddleware} from 'redux'
import thunk from 'redux-thunk' //to generate middleware
import { restaurantListReducer } from './Reducers/restaurantsReducer'

const reducers=combineReducers({//2nd method
    restaurantReducer:restaurantListReducer //key:function

})
const middleware=[thunk]
const store=createStore(reducers,applyMiddleware(...middleware))//1st method

export default store