import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import ListGroup from 'react-bootstrap/ListGroup';


function Restoperate({operate}) {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    console.log(operate);
  return (
    <>
{
        <><Button variant="dark" onClick={handleShow} style={{ marginLeft: '210px' ,borderRadius:'20px'}}>
          Operating-Hours
        </Button>
        <Modal show={show} onHide={handleClose}>
            <Modal.Header>
              <Modal.Title>Timing:</Modal.Title>
            </Modal.Header>
            <Modal.Body >
              <ListGroup >
                <ListGroup.Item style={{backgroundColor:'black',color:'white'}}>Monday:{operate.Monday}</ListGroup.Item>
                <ListGroup.Item>Tuesday:{operate.Tuesday}</ListGroup.Item>
                <ListGroup.Item style={{backgroundColor:'black',color:'white'}}>Wednesday:{operate.Wednesday}</ListGroup.Item>
                <ListGroup.Item>Thursday:{operate.Thursday}</ListGroup.Item>
                <ListGroup.Item style={{backgroundColor:'black',color:'white'}}>Friday:{operate.Friday}</ListGroup.Item>
                <ListGroup.Item>Saturday:{operate.Saturday}</ListGroup.Item>
                <ListGroup.Item style={{backgroundColor:'black',color:'white'}}>Sunday:{operate.Sunday}</ListGroup.Item>
              </ListGroup>
            </Modal.Body>
          </Modal></>
}
    </>
  )

}

export default Restoperate
